.partners-page{
  padding: 2.75em 0 5.625em;
  overflow: hidden;
}

.partners-tabs{
  margin-top: 1.5em;
  display: flex;
  flex-wrap: wrap;
  &__item{
    margin: .75em .75em 0 0;
    width: 10.625em;
    height: 4.0625em;
    background: #efefef;
    border-radius: .75em;
    border: 1px solid #dcdcdc;
    @include center;
    padding: .5em;
    cursor: pointer;
    &:not(._active){
      border-color: #efefef;
      img{
        filter: grayscale(100%);
        opacity: .5;
      }
    }
    img{
      transition: all .3s;
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.partners-slider{
  margin-top: 2.75em;
  &__slide{
    width:  72.125em;
    padding-right: .9375em;
  }
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
  }
}

.partner-card{
  padding: 3.375em 3.875em 4em;
  background-color: #fff;
  border-radius: 2.5em;
  display: grid;
  grid-template-columns: 7.625em calc(100% - 10.25em);
  justify-content: space-between;
  color: #737373;
  overflow: hidden;
  height: 100%;
  &__img{
    width: 100%;
    display: block;
  }
  &__col2{
    padding-top: .875em;
  }
  &__tags,
  &__link,
  &__text{
    font-size: 1em;
    line-height: 164%;
  }
  &__links{
    display: flex;
    margin-top: 1.25em;
  }
  &__link{
    @include lastM(0 2em 0 0);
    font-weight: 600;
  }
  &__tags{
    margin-top: .875em;
    font-weight: 500;
    color: $primary;
    span{
      margin-right: 1em;
      display: inline-block;
    }
  }
  .gallery-slider{
    margin-top: 2.25em;
  }
}

.gallery-slider{
  .swiper{
    &-slide{
      width: 21.5em;
      padding-right: .875em;
    }
    &-button{
      display: none;
    }
  }
  &__img{
    @include imgCover(66.7%);
    border-radius: 1.0625em;
    &:not(:hover):after{
      opacity: 0;
    }
    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
      background-image: url(../img/zoom.svg);
      @include bgImgSize(3.25em);
      transition: all .3s;
    }
  }
}


@media screen and (max-width: 992px){
  .partners-page{
    padding: 1.5em 0 4.125em;
  }

  .partners-tabs{
    margin-top: 1.875em;
    margin-left: -.9375em;
    padding: 0 1.875em;
    width: calc(100% + 1.875em);
    overflow-x: auto;
    flex-wrap: nowrap;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
    &__item{
      height: 3.125em;
      width: 8.125em;
      margin: 0 .3125em 0 0;
      flex-shrink: 0;
    }
  }

  .partners-slider{
    margin-top: 1.125em;
    &__slide{
      width: 90%;
    }
  }

  .partner-card{
    border-radius: 1.25em;
    padding: 1.625em 1em 2.25em;
    grid-template-columns: 100%;
    gap: 1.625em;
    &__img{
      width: auto;
      height: 3em;
    }
    &__col2{
      padding: 0;
    }
    &__link{
      @include lastM(0 .875em 0 0);
    }
    &__tags{
      margin-top: 1em;
      font-size: .875em;
      span{
        margin-right: .75em;
      }
    }
    .gallery-slider{
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .partners-slider{
    &__container{
      width: calc(100% + .9375em);
    }
    &__slide{
      width: 100%;
    }
  }

  .gallery-slider{
    position: relative;
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        width: 100%;
        padding-right: 1.25em;
      }
      &-button{
        display: flex;
        width: .625em;
        height: .9375em;
        top: 50%;
        transform: translateY(-50%);
        margin: 0;
        svg{
          width: 100%;
        }
      }
    }
  }
}
