.header{
  padding: 1.625em 0;
  border: 1px solid #eaeaea;
  position: relative;
  z-index: 20;
  &__inner{
    display: flex;
    align-items: center;
    padding-right: 18.25em;
    position: relative;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 11.5em;
    margin-right: 2em;
    img{
      width: 100%;
      display: block;
    }
  }
  @media screen and (min-width: 993px){
    &--white{
      background-color: #fff;
    }
    font-size: calc(1400px * (16 / 1400));
    @media screen and (max-width: 1400px){
      font-size: calc(100vw * (16 / 1400));
    }
    .container{
      max-width: 1400px;
    }
  }
}

.header-info{
  position: absolute;
  right: 8.75em;
  top: -1.5em;
}

.header-mob{
  display: none;
}

.menu{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  &__bottom{
    margin-top: .75em;
    display: flex;
    align-items: center;
  }
  &__buttons{
    margin-left: 2.5em;
    display: flex;
    .m-btn-light-red{
      padding: 0 1.375em;
    }
  }
  &__btn{
    @include lastM(0 .5em 0 0);
    padding: 0 1.125em;
    img{
      height: 1.0625em;
      margin-right: .5em;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    display: flex;
    align-items: center;
    font-weight: 500;
    @include lastM(0 2em 0 0);
    &:hover{
      color: $primary;
      path{
        fill: $primary;
      }
    }
    svg{
      height: 1em;
      margin-right: .5625em;
    }
    span{
      font-size: 1em;
      line-height: 119%;
    }
  }
}

.nav2{
  display: flex;
  align-items: center;
  &__link{
    @include lastM(0 2.25em 0 0);
    font-size: 1em;
    line-height: 119%;
    color: #a1a1a1;
    &:hover{
      color: $black;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: .625em 0;
    border: none;
    &__inner{
      padding: 0;
      justify-content: space-between;
    }
    &__logo{
      margin: 0;
      width: 5.1875em;
      position: relative;
      z-index: 12;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
        path{
          fill: $black
        }
      }
    }
  }

  .menu{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #F6F6F6;
    padding: 5em 1em 1em;
    align-items: center;
    overflow-y: auto;
    z-index: 11;
    &__bottom,
    &__buttons{
      flex-direction: column;
      align-items: center;
    }
    &__buttons{
      margin-top: 1em;
      margin-left: 0;
    }
    &__btn{
      @include lastM(0 0 .75em);
    }
  }

  .nav,
  .nav2{
    flex-direction: column;
    align-items: center;
    &__link{
      @include lastM(0 0 1.25em);
    }
  }

  .header-info{
    position: static;
    z-index: 12;
    .call-info__bottom,
    .call-info__img{
      display: none;
    }
    .call-info__content{
      position: static;
    }
    .call-info__top{
      &:before{
        content: '';
        flex-shrink: 0;
        width: 1.0625em;
        height: 1.0625em;
        background-image: url(../img/phone.svg);
        @include bgImgSize(100%);
        margin-right: .375em;
      }
      span{
        font-size: 1.5em;
        line-height: 121%;
      }
      img{
        display: none;
      }
    }
  }

  .header-mob{
    display: flex;
    position: relative;
    z-index: 12;
    &__btn{
      width: 1.875em;
      height: 1.875em;
      @include center;
      @include lastM(0 .25em 0 0);
      img{
        width: 100%;
        display: block;
      }
    }
  }
}


