.become-partner{
  padding: 5em 4.5em 6.5625em 30em;
  background-color: $yellow;
  border-radius: 2.5em;
  margin-top: 3.5em;
  overflow: hidden;
  position: relative;
  &__img{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 33.9375em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__content{
    @include grid100gap(1.9375em);
  }
  &__title{
    font-size: 2.125em;
    line-height: 120%;
    font-weight: 600;
    font-family: $font2;
  }
  &__round{
    width: 11.625em;
    height: 11.625em;
    background-color: #fff;
    box-shadow: 0 4px 68px 0 rgba(0, 0, 0, 0.25);
    border-radius: 50%;
    display: block;
    position: absolute;
    right: -3.1875em;
    bottom: -4.125em;
    padding: 3.25em;
  }
}

.become-partner2{
  .become-partner__content{
    max-width: 35em;
  }
  .become-partner__desc{
    font-weight: 600;
    font-size: 1.625em;
  }
}

.partner-info{
  width: 48.75em;
  max-width: 100%;
  margin: 4.625em auto 0;
  text-align: center;
  font-weight: 600;
  font-family: $font2;
  &__text{
    font-size: 1.25em;
    line-height: 125%;
  }
  &__text2{
    font-size: 1.5em;
    line-height: 121%;
    color: $primary;
    padding: 0 2em;
  }
  &__arrow{
    display: block;
    margin: .75em auto 1em;
    width: 1em;
  }
}

.partner-props{
  margin-top: 7.1875em;
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 6.5em / 3));
  gap: 2.125em 3.25em;
}

.driver-step,
.partner-prop{
  &__num{
    flex-shrink: 0;
    margin-top: .125em;
    margin-right: 1.625em;
    width: 3.0625em;
    height: 3.0625em;
    @include center;
    border-radius: .6875em;
    background-color: $primary;
    color: #fff;
    font-weight: 700;
    font-family: $font2;
    span{
      font-size: 1.5em;
    }
  }
}

.partner-prop{
  display: flex;
  align-items: flex-start;
  &__content{
    @include grid100gap(1.375em);
  }
  &__title{
    font-weight: 700;
    font-family: $font2;
    color: $primary;
  }
  &__desc{
    font-size: 1em;
    line-height: 150%;
    color: #6d6d6d;
  }
}


@media screen and (max-width: 992px){
  .become-partner{
    font-size: .625em;
  }

  .partner-info{
    margin-top: 2.125em;
    &__text{
      font-size: 1em;
    }
    &__text2{
      font-size: 1.25em;
      padding: 0;
    }
    &__arrow{
      margin: 1.25em auto 1em;
    }
  }

  .partner-props{
    margin-top: 3.625em;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.25em 1.875em;
  }

  .partner-prop{
    &__num{
      margin-top: .1875em;
      margin-right: .9375em;
      width: 2.125em;
      height: 2.125em;
      border-radius: .5625em;
      span{
        font-size: 1em;
      }
    }
    &__content{
      gap: .5625em;
    }
    &__title{
      font-size: 1.125em;
    }
    &__desc{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .become-partner{
    margin-top: 2.75em;
    font-size: 1em;
    padding: 3.3125em 2em 16.5625em;
    &__img{
      left: -1.5em;
      bottom: -9em;
      width: 21.5625em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__content{
      gap: 1.25em;
    }
    &__title{
      font-size: 1.5em;
    }
    &__desc{
      font-size: 1em;
    }
    &__round{
      display: none;
    }
  }

  .become-partner2{
    padding-right: 1.25em;
    margin-top: 2.125em;
    .become-partner__desc{
      font-size: 1.25em;
    }
    .become-partner__img{
      bottom: 0;
      left: 0;
    }
  }

  .partner-props{
    grid-template-columns: 100%;
  }
}
