.news{
  padding: 3.375em 0 3.125em;
  &__inner{
    display: grid;
    grid-template-columns: 37.5em calc(100% - 41em);
    gap: 3.5em;
    align-items: center;
  }
  &__items{
    max-width: 33.875em;
  }
}

.news-info{
  position: relative;
  padding-left: 10.8125em;
  &__img{
    &-wrap{
      position: relative;
      overflow: hidden;
      border-radius: 2.625em;
    }
    img{
      border-radius: 2.625em 2.625em 2.75em 2.625em;
      width: 100%;
      display: block;
    }
  }
  &__content{
    position: absolute;
    z-index: 2;
    left: 0;
    width: 22.5em;
    top: 50%;
    transform: translateY(-50%);
    @include grid100gap(1.625em);
  }
  &__desc{
    line-height: 169%;
  }
}

.news-item{
  padding: 1.875em 0 1.25em;
  border-bottom: 1px solid #d8d8d8;
  @include grid100gap(1.125em);
  background-image: url(../img/arrow3.svg);
  background-repeat: no-repeat;
  background-size: 1.25em auto;
  background-position: right 0 top 2.5em;
  &__top{
    max-width: 23.75em;
  }
  &__desc{
    line-height: 168%;
    @include truncate(2);
  }
}

.text{
  p{
    font-size: 1em;
    line-height: 150%;
    @include lastM(0 0 2em);
  }
}


@media screen and (max-width: 992px){
  .news{
    padding: 1.875em 0 2.125em;
    &__inner{
      grid-template-columns: 21.5625em calc(100% - 21.5625em - 1.25em);
      gap: 4.5em 1.25em;
    }
  }

  .news-info{
    padding-left: 3.125em;
    &__img{
      &-wrap{
        border-radius: 1.25em 1.25em 2.875em 1.25em;
      }
      img{
        border-radius: 1.25em 1.25em 2.875em 1.25em;
      }
    }
    &__content{
      width: 15.5em;
      transform: unset;
      top: 1.625em;
      gap: 1.125em;
    }
    &__title{
      font-size: 1.875em;
    }
    &__desc{
      font-size: .875em;
    }
  }

  .news-item{
    padding-top: .9375em;
    gap: .875em;
    background-position: right 0 top 1.25em;
    &__top{
      max-width: 15em;
    }
    &__title{
      font-size: 1em;
      line-height: 125%;
    }
    &__desc{
      font-size: .875em;
      line-height: 154%;
    }
  }
}


@media screen and (max-width: 576px){
  .news{
    &__inner{
      grid-template-columns: 100%;
    }
    &__col{
      order: 2;
    }
  }

  .news-main2{
    .news-main__content{
      padding-left: .875em;
      padding-bottom: 0;
    }
  }
}
