.footer{
  &__inner{
    clip-path: polygon( 0% 4.89%,0% 4.89%,0.025% 4.078%,0.097% 3.311%,0.212% 2.6%,0.366% 1.957%,0.554% 1.391%,0.772% 0.914%,1.016% 0.536%,1.283% 0.268%,1.567% 0.121%,1.864% 0.106%,98.364% 17.401%,98.364% 17.401%,98.634% 17.507%,98.888% 17.719%,99.123% 18.03%,99.337% 18.429%,99.527% 18.907%,99.689% 19.457%,99.821% 20.069%,99.918% 20.733%,99.979% 21.442%,100% 22.185%,100% 108.562%,100% 108.562%,99.977% 109.339%,99.911% 110.077%,99.805% 110.765%,99.662% 111.393%,99.488% 111.952%,99.284% 112.431%,99.054% 112.821%,98.803% 113.112%,98.534% 113.293%,98.25% 113.356%,1.75% 113.356%,1.75% 113.356%,1.466% 113.293%,1.197% 113.112%,0.946% 112.821%,0.716% 112.431%,0.513% 111.952%,0.338% 111.393%,0.195% 110.765%,0.089% 110.077%,0.023% 109.339%,0% 108.562%,0% 4.89% );
    min-height: 27.75em;
    background-image: url(../img/footer-bg.webp);
    background-size: cover;
    background-position: center;
    padding: 2.75em 3.5em 3.5em;
  }
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  &__email{
    display: inline-block;
    font-size: 1.25em;
    line-height: 125%;
    font-weight: 700;
  }
  &__buttons{
    margin-top: 1.25em;
    width: 12.5em;
    @include grid100gap(.4375em);
  }
  &__btn{
    img{
      height: 1.0625em;
      margin-right: .5em;
    }
  }
  &__bottom{
    display: flex;
    margin-top: 2.5em;
  }
  &__item{
    @include lastM(0 5em 0 0);
    font-size: .9375em;
    line-height: 113%;
    color: #a0a0a0;
    font-weight: 500;
    a{
      text-decoration: underline;
      text-decoration-skip-ink: none;
    }
  }
}

.call-info{
  color: $primary;
  &__img{
    width: 9.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    position: absolute;
    top: .875em;
    left: 6.8125em;
  }
  &__top{
    font-family: $font4;
    white-space: nowrap;
    display: flex;
    align-items: center;
    span{
      font-size: 1.875em;
    }
    img{
      width: .875em;
      margin-left: .4375em;
    }
  }
  &__bottom{
    text-align: right;
    margin: .3125em -.5em 0 0;
  }
  &__desc{
    font-size: .9375em;
    line-height: 120%;
  }
}

.footer-info{
  position: relative;
  margin-bottom: 1.375em;
}

.footer-nav{
  display: flex;
  align-items: flex-end;
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    @include lastM(0 4.125em 0 0);
  }
  &__link{
    @include lastM(0 0 1.28em);
    font-size: 1.125em;
    line-height: 122%;
    font-weight: 500;
  }
  &__link2{
    @include lastM(0 0 1.625em);
    font-size: 1em;
    line-height: 119%;
    font-weight: 500;
    color: #7e7e7e;
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__inner{
      padding: 4.125em 1.25em 1.5em;
    }
    &__col2{
      display: none;
    }
    &__bottom{
      flex-direction: column;
      align-items: center;
      margin-top: 2.375em;
    }
    &__item{
      @include lastM(0 0 .57em);
      font-size: .875em;
      line-height: 121%;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__inner{
      clip-path: polygon( 0% 4.08%,0% 4.08%,0.095% 3.372%,0.37% 2.709%,0.807% 2.098%,1.388% 1.551%,2.096% 1.076%,2.915% 0.685%,3.826% 0.385%,4.812% 0.189%,5.856% 0.104%,6.941% 0.142%,94.768% 8.278%,94.768% 8.278%,95.643% 8.402%,96.464% 8.604%,97.221% 8.877%,97.906% 9.214%,98.509% 9.608%,99.023% 10.054%,99.437% 10.544%,99.744% 11.072%,99.935% 11.632%,100% 12.216%,100% 95.9%,100% 95.9%,99.92% 96.545%,99.69% 97.157%,99.321% 97.728%,98.826% 98.249%,98.217% 98.713%,97.508% 99.11%,96.71% 99.434%,95.837% 99.675%,94.9% 99.825%,93.913% 99.877%,6.087% 99.877%,6.087% 99.877%,5.1% 99.825%,4.163% 99.675%,3.29% 99.434%,2.492% 99.11%,1.783% 98.713%,1.174% 98.249%,0.679% 97.728%,0.31% 97.157%,0.08% 96.545%,0% 95.9%,0% 4.08% );
    }
    &__top{
      flex-direction: column;
      align-items: center;
    }
    &__col{
      width: 100%;
      text-align: center;
    }
    &__col3{
      margin-top: 1.875em;
    }
    &__buttons{
      margin-left: auto;
      margin-right: auto;
    }
  }
}
