.manager{
  width: 42.0625em;
  max-width: 100%;
  margin-top: -11.125em;
  margin-left: auto;
  position: relative;
  &__mask{
    width: 100%;
    display: block;
    &._view2{
      display: none;
    }
  }
  &__content{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    padding: 5.25em 2.5em 2em 12em;
  }
  &__text{
    font-size: 1em;
    line-height: 153%;
    font-weight: 500;
    span{
      font-weight: 700;
      font-family: $font2;
    }
  }
  &__img{
    position: absolute;
    width: 21.5em;
    top: -3.625em;
    left: -5.25em;
  }
}

.partner-req{
  margin-top: 6.25em;
  display: grid;
  grid-template-columns: 32.5em 30em;
  justify-content: space-between;
  gap: 2.25em;
  &__col{
    padding-top: 1.5em;
  }
  &__subtitle{
    font-weight: 600;
    font-size: 1.375em;
    line-height: 123%;
    font-family: $font2;
    max-width: 21em;
  }
  &__title{
    margin-top: .32em;
  }
  &__images{
    margin-top: 3.375em;
    position: relative;
    width: 26.625em;
    max-width: 100%;
    padding-left: 5.25em;
  }
  &__img{
    display: block;
    width: 100%;
  }
  &__arrow{
    position: absolute;
    left: 100%;
    top: 0;
    width: 14em;
  }
  &__arrow2{
    display: none;
  }
}

.partner-form{
  background-color: #fff;
  padding: 3.125em 3.5em;
  border-radius: 2.5em;
  @include grid100gap(.375em);
  &__btn{
    margin-top: 1.5em;
  }
}

.driver{
  padding: 3.75em 0 6.25em;
  &__top{
    text-align: center;
    @include grid100gap(.625em);
  }
  &__wrap{
    margin: 4.375em auto 0;
    width: 54.5625em;
    max-width: 100%;
    padding: 2.625em 2em 2em 3.125em;
    border-radius: 2.5em;
    background-color: $yellow;
    display: flex;
    align-items: center;
    position: relative;
    overflow: hidden;
    &:before{
      content: '';
      position: absolute;
      background-color: #fff;
      border-radius: 50%;
      width: 24.375em;
      height: 24.375em;
      bottom: -10.1875em;
      left: -6.875em;
    }
  }
  &__img{
    width: 11.125em;
    flex-shrink: 0;
    position: relative;
    z-index: 2;
    margin-right: 5.375em;
  }
  &__desc{
    font-weight: 500;
    font-family: $font2;
    font-size: 1em;
    line-height: 153%;
  }
}

.driver-process{
  margin-top: 4.375em;
  &__steps{
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 1.625em * 4 / 5));
    gap: 1.625em;
    margin-top: 3.625em;
  }
}

.driver-step{
  &__num{
    background-color: $yellow;
    margin: 0 0 1em;
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
    color: #757575;
    font-weight: 700;
    font-family: $font2;
  }
}


@media screen and (max-width: 992px){
  .manager{
    width: 21.5625em;
    margin-top: -8.125em;
    &__mask{
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
    &__content{
      padding: 5.5em .9375em 2em 3.5em;
    }
    &__img{
      width: 15.125em;
      top: -2.3125em;
      left: -6.5em;
    }
  }

  .partner-req{
    grid-template-columns: 100%;
    margin-top: 10.5em;
    &__col{
      position: relative;
      max-width: 32em;
    }
    &__images{
      padding: 0;
      position: absolute;
      right: -.9375em;
      bottom: 100%;
      margin: 0 0 -1em;
      width: 10.5em;
    }
    &__arrow{
      display: none;
    }
    &__arrow2{
      display: block;
      position: absolute;
      width: 5.9375em;
      bottom: .875em;
      right: 100%;
      margin-right: 1.25em;
    }
    &__subtitle{
      font-size: 1em;
    }
    &__title{
      font-size: 1.5em;
      margin-top: .54em;
    }
  }

  .driver{
    padding: .5em 0 4.375em;
    &__top{
      gap: .8125em;
      max-width: 17em;
      margin: 0 auto;
    }
    &__wrap{
      margin-top: 2.5em;
    }
  }

  .driver-process{
    margin-top: 3em;
    &__steps{
      grid-template-columns: repeat(3,calc(100% / 3 - 1em));
      gap: 1.5em;
      margin-top: 2.125em;
    }
  }

  .driver-step{
    &__num{
      width: 2.125em;
      height: 2.125em;
      border-radius: .5625em;
      margin-bottom: .5em;
      span{
        font-size: 1em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .manager{
    width: 100%;
    margin-top: 2.25em;
  }

  .partner-form{
    border-radius: 1.25em;
    padding: 2.1875em 1.1875em 2.75em;
    gap: .3125em;
    &__btn{
      margin-top: 1em;
    }
  }

  .driver{
    &__wrap{
      padding: 1.875em 1.75em .875em;
      flex-direction: column;
      text-align: center;
      &:before{
        bottom: -15em;
      }
    }
    &__img{
      order: 2;
      width: 13.125em;
      margin-top: 2.875em;
      margin-left: 1.875em;
    }
  }

  .driver-process{
    &__title{
      font-size: 1.5em;
      text-align: left;
    }
    &__steps{
      grid-template-columns: 100%;
    }
  }

  .driver-step{
    display: flex;
    align-items: center;
    &__num{
      margin: 0 .9375em 0 0;
    }
  }
}
