.driver-form{
  background-color: #fff;
  padding: 3.125em 3.5em;
  border-radius: 2.5em;
  &__types{
    display: flex;
    align-items: center;
    margin-bottom: 1.75em;
    .m-radio{
      margin-right: 1.75em;
    }
  }
  &__fields{
    @include grid100gap(.375em);
  }
  &__btn{
    width: 100%;
    margin-top: 2em;
  }
}

.driver-type{
  display: none;
  &._active{
    display: block;
  }
}

.driver-transport{
  margin-top: 1.75em;
  &__title{
    font-size: 1.125em;
    line-height: 122%;
    font-weight: 600;
    font-family: $font2;
  }
  &__group{
    margin-top: 1em;
  }
  &__subtitle{
    font-size: 1em;
    line-height: 125%;
    font-family: $font2;
  }
  &__items{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + .4375em);
  }
  .transport-field{
    margin: .4375em .4375em 0 0;
  }
}

.transport-field{
  padding: 0 1.0625em;
  display: flex;
  align-items: center;
  height: 2.5em;
  input:checked{
    &~.transport-field__title{
      color: $primary;
      &:before{
        border-color: $primary;
      }
    }
    &~.transport-field__icon{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
  &__title{
    font-size: 1em;
    font-weight: 700;
    font-family: $font2;
    color: #606060;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 1px solid #e0e0e0;
      border-radius: .75em;
    }
  }
  &__icon{
    height: 1.3125em;
    width: auto;
    margin-left: .625em;
    position: relative;
    transition: all .3s;
    img{
      height: 100%;
      display: block;
    }
    ._view2{
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

.transport-field2{
  .transport-field__icon{
    height: 1.125em;
  }
}

.partner-location{
  margin-top: 5.3125em;
  background-color: $yellow;
  padding: 3.875em 13.25em 4.6875em 30em;
  border-radius: 2.5em;
  @include grid100gap(1.625em);
  position: relative;
  overflow: hidden;
  &:before{
    content: '';
    position: absolute;
    width: 30.3125em;
    height: 30.3125em;
    background-color: #fff;
    border-radius: 50%;
    left: -16.875em;
    bottom: -13.25em;
    box-shadow: 0 4px 68px 0 rgba(0, 0, 0, 0.25);
  }
  .h2{
    max-width: 95%;
  }
  &__desc{
    b{
      font-weight: 600;
    }
  }
  &__img{
    top: -.75em;
    left: 1.6875em;
    width: 23.8125em;
    position: absolute;
  }
  &__arrow{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 20.5em;
    width: 6.6875em;
  }
}


@media screen and (max-width: 992px){
  .partner-location{
    font-size: .6875em;
    padding-right: 8em;
  }
}


@media screen and (max-width: 576px){
  .driver-form{
    padding: 1.6875em 1.1875em 2.75em;
    border-radius: 1.25em;
  }

  .driver-transport{
    &__items{
      flex-wrap: nowrap;
      width: calc(100% + 2.375em);
      margin-left: -1.1875em;
      padding: 0 1.1875em;
      overflow-x: auto;
      @include nullScroll;
    }
  }

  .transport-field{
    flex-shrink: 0;
    &__icon{
      display: none;
    }
  }

  .partner-location{
    font-size: 1em;
    padding: 3.4375em 1.875em 14.375em;
    gap: 1.5em;
    margin-top: 4.25em;
    &:before{
      width: 15.1875em;
      height: 15.1875em;
      left: -3.875em;
      bottom: -4.875em;
    }
    &__arrow{
      display: none;
    }
    .h2{
      font-size: 1.375em;
      max-width: 105%;
      width: 105%;
    }
    &__img{
      width: 19.5625em;
      top: unset;
      bottom: -4.4375em;
      left: unset;
      right: 0;
    }
  }
}
