@import "../vars.scss";
.breadcrumbs {
    display: none;
    li{
        display: inline;
        color: #cdcdcd;
        font-size: 1em;
        line-height: 167%;
        &:last-child{
            color: $primary;
        }
        &:not(:first-child){
            &:before{
                content: '| ';
                color: #cdcdcd;
            }
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        display: block;
        margin-bottom: .625em;
    }
}


