.news-main{
  padding: 2.75em 0 2em;
  &__inner{
    display: grid;
    grid-template-columns: 50% 50%;
    margin-top: 4em;
    border-radius: 2.5em;
    background-color: #fff;
  }
  &__img{
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: 2.5em 60% 2.5em 2.5em;
    }
  }
  &__content{
    padding: 4em 2.75em 4em 4.375em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__top{
    max-width: 25em;
  }
  .h2{
    font-weight: 600;
  }
  &__desc,
  &__link,
  &__date{
    font-size: 1em;
    line-height: 168%;
  }
  &__date,
  &__desc{
    margin-top: 1.375em;
  }
  &__date{
    color: #bfbfbf;
  }
  &__link{
    font-weight: 600;
    color: $primary;
    margin-top: 2.25em;
    &:hover{
      opacity: .8;
    }
  }
  &__text{
    padding: 4.375em 6.25em 6.625em;
    grid-column: 1/-1;
  }
  &__bottom{
    display: flex;
    margin-top: 3.25em;
  }
  &__back{
    display: flex;
    align-items: center;
    font-family: $font2;
    font-weight: 700;
    font-size: 1em;
    img{
      width: 1.9375em;
      margin-right: .6875em;
    }
  }
}

.page-title{
  display: flex;
  align-items: flex-start;
  &__icon{
    width: 3.5em;
    margin-right: 1.25em;
    margin-top: 1.0625em;
  }
}

.last-news{
  padding: 2em 0 3.375em;
  overflow: hidden;
  &:last-child{
    padding-bottom: 6.25em;
  }
}

.news-slider{
  margin-top: 2.625em;
  .swiper{
    &-slide{
      width: 39.375em;
      padding-right: 1.875em;
    }
  }
}

.news-card,
.news-info{
  &__round{
    position: absolute;
    width: 7.4375em;
    height: 7.4375em;
    right: -1.5em;
    bottom: -2.625em;
    background-color: #fff;
    box-shadow: 0 .25em 4.25em rgba(0, 0, 0, 0.25);
    border-radius: 50%;
    z-index: 2;
    padding: 1.875em 1em 1em 2.375em;
    img{
      width: 1.75em;
      display: block;
    }
  }
}

.news-card{
  display: block;
  position: relative;
  border-radius: 2.5em;
  overflow: hidden;
  &__img{
    @include imgCover(61.5%);
    border-radius: 2.625em 2.625em 2.75em 2.625em;
    &:after{
      background: linear-gradient(180.00deg, rgba(0, 0, 0, 0),rgba(0, 0, 0, 0.46) 100%);    }
  }
  &__content{
    color: #fff;
    font-weight: 600;
    font-family: $font2;
    z-index: 2;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 2.875em 9.5em 2.875em 2.875em;
  }
}

.promos{
  padding: 3.125em 0 7.5em;
  overflow: hidden;
}

.promo-card{
  &:after{
    content: '';
    position: absolute;
    top: 12.5em;
    left: -6.625em;
    width: 49em;
    height: 18.375em;
    background-color: $yellow;
    transform: rotate(14deg);
  }
  .news-card__content{
    color: $black;
  }
}


@media screen and (max-width: 992px){
  .news-main{
    padding: 1.5em 0 1.25em;
    &__inner{
      margin-top: 2.125em;
    }
    &__content{
      padding: 2.125em 1.5em;
    }
    &__top{
      max-width: 16.25em;
    }
    &__desc{
      font-size: .875em;
      margin-top: 1.25em;
    }
    &__link{
      margin-top: .625em;
    }
    &__date{
      margin-top: .75em;
    }
    &__text{
      padding: 2.125em 1.5em;
    }
    &__bottom{
      margin-top: 2.375em;
    }
  }

  .page-title{
    &__icon{
      width: 2.375em;
      margin-right: .75em;
      margin-top: .875em;
    }
  }

  .last-news{
    padding: 1.25em 0 1.875em;
    &:last-child{
      padding-bottom: 4.25em;
    }
  }

  .news-slider{
    margin-top: 1.5em;
    .swiper-slide{
      width: 19em;
      padding-right: .625em;
    }
  }

  .news-card,
  .news-info{
    &__round{
      width: 4.6875em;
      height: 4.6875em;
      padding: 1.125em 1.25em;
      right: -1.25em;
      bottom: -1.625em;
      img{
        width: 1.25em;
      }
    }
  }

  .news-card{
    border-radius: 1.25em;
    &__img{
      padding-bottom: 72.4%;
      border-radius: 1.25em 1.25em 2.875em 1.25em;
    }
    &__content{
      padding: 1.875em 4em 1.875em 1.625em;
    }
    &__title{
      font-size: 1em;
      line-height: 125%;
    }
  }

  .promos{
    padding: 2.125em 0 4.25em;
    .page-title{
      display: none;
    }
    .news-slider{
      margin: 0;
    }
  }

  .promo-card{
    &:after{
      top: 6em;
      left: -7em;
      width: 32.9375em;
      height: 12.375em;
    }
    .news-card__content{
      padding: .125em 5em 1.25em 2em;
    }
    &__num{
      font-size: 2.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .news-main{
    &__inner{
      grid-template-columns: 100%;
    }
    &__img{
      img{
        border-radius: 2.5em 2.5em 50% 2.5em;
      }
    }
    &__text{
      padding: 1.75em .875em 2.5em;
    }
  }
}
