@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1em;
    height: 2.25em;
    border-radius: .5625em;
    font-weight: 500;
    span{
        font-size: 1em;
    }
    &:focus {
        box-shadow: none;
    }
    &--big{
        height: 3.75em;
        font-weight: 600;
        font-family: $font2;
        border-radius: .75em;
    }
}

.m-btn-yellow{
    background-color: $yellow;
    color: $black;
    &:hover{
        background-color: darken($yellow,10%);
        color: $black;
    }
}

.m-btn-red{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: #e75858;
        color: #fff;
    }
}

.m-btn-light-red{
    background-color: #e9333314;
    color: $primary;
    &:hover{
        background-color: rgba($primary,.29);
        color: $primary;
    }
}


@media screen and (max-width: 576px){
    .m-btn{
        &--big{
            height: 3.125em;
        }
    }
}
