@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 3em;
    height: 3em;
    @include lastM(0 .9375em 0 0);
    svg{
      width: 100%;
    }
    &:hover{
      ._fill{
        fill: darken($yellow,10%);
      }
    }
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    pointer-events: none;
    border-radius: inherit;
  }
}
