.loyalty-page{
  padding: 2.75em 0 5.3125em;
  overflow: hidden;
}

.loyalty-main{
  min-height: 23.125em;
  margin-top: 3.875em;
  display: block;
  @media screen and (min-width: 993px){
    padding: 3.25em 11.5em 3em 27.1875em;
  }
  &__title{
    font-weight: 600;
    font-family: $font2;
    font-size: 4.375em;
    line-height: 137%;
  }
  &__desc{
    font-size: 1em;
    line-height: 167%;
    font-weight: 500;
    margin-top: 1.8125em;
  }
  &__coins{
    position: absolute;
    top: 1.8125em;
    right: 1.0625em;
    width: 11em;
  }
}

.loyalty-advantages{
  margin-top: 4.5em;
  width: 25.5em;
  .swiper{
    &-container{
      width: calc(100% + 1.625em);
    }
    &-wrapper{
      align-items: center;
    }
    &-slide{
      padding-right: 1.625em;
      width: 100%;
      min-height: 32.8125em;
      display: flex;
      align-items: center;
      &:not(.swiper-slide-active){
        .advantage-card2{
          &__text{
            display: none;
          }
        }
      }
    }
    &-pagination{
      display: none;
    }
  }
}

.advantage-card2{
  width: 100%;
  padding: 2.625em 2em 3.5em;
  box-shadow: 0 12px 19px 0 rgba(0, 0, 0, 0.11);
  border-radius: 2.5em;
  color: #fff;
  transition: all .3s;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    background: linear-gradient(141deg, #000 24.99%, #787878 100%);
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    z-index: -1;
    transition: all .3s;
  }
  &:hover{
    background-color: $yellow;
    color: $black;
    .advantage-card2__text{
      color: inherit;
    }
    &:before{
      opacity: 0;
    }
  }
  &__icon{
    height: 8.25em;
    display: flex;
    align-items: center;
    margin-bottom: 1.75em;
    img{
      max-height: 100%;
      display: block;
    }
  }
  &__title{
    font-size: 1.875em;
    line-height: 123%;
    font-weight: 600;
    font-family: $font2;
  }
  &__text{
    margin-top: 1.625em;
    color: #d6d6d6;
    p{
      font-size: 1em;
      line-height: 169%;
      @include lastM(0 0 1.25em);
    }
  }
}

.loyalty-advantages2{
  margin-top: 1.5em;
  display: grid;
  grid-template-columns: 25em 44.8125em;
  justify-content: space-between;
  align-items: flex-end;
  gap: 3.125em 1.25em;
  &__col{
    padding-bottom: 1em;
  }
}

.bonus{
  padding: 3.75em 2.875em 11.625em;
  background: #fff;
  border-radius: 2.5em;
  font-family: $font2;
  font-weight: 600;
  position: relative;
  &__subtitle{
    font-size: 1.375em;
    line-height: 123%;
    margin-bottom: .5em;
  }
  &__img{
    position: absolute;
    bottom: 0;
    left: -1.375em;
    width: 18.75em;
  }
}

.call-order{
  padding-left: 18.125em;
  position: relative;
  &__info{
    position: absolute;
    top: 9.375em;
    left: 0;
    width: 28.125em;
    @include grid100gap(1.625em);
  }
  &__desc{
    font-size: 1em;
    line-height: 168%;
  }
  &__img{
    width: 100%;
    display: block;
    &._view2{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .loyalty-advantages{
    margin-top: 3.875em;
    width: 19.25em;
    .swiper{
      &-container{
        width: calc(100% + 1.125em);
      }
      &-slide{
        padding-right: 1.125em;
        min-height: 27.5em;
      }
    }
  }

  .advantage-card2{
    width: 100%;
    padding: 1.75em 2em 2em;
    &__icon{
      height: 4.75em;
      margin-bottom: 1.25em;
    }
    &__title{
      font-size: 1.5em;
    }
    &__text{
      margin-top: 1.8125em;
      p{
        font-size: .875em;
        line-height: 167%;
        @include lastM(0 0 1.35em);
      }
    }
  }

  .loyalty-advantages2{
    grid-template-columns: 21.4375em calc(100% - 21.4375em - 1.25em);
    margin-top: 5.75em;
  }

  .bonus{
    padding: 2.625em 1.5em 6em 2.125em;
    &__img{
      width: 14.1875em;
      left: -1.375em;
      bottom: -2.75em;
    }
    &__subtitle{
      font-size: 1.25em;
      margin-bottom: .7em;
    }
    &__title{
      font-size: 2.5em;
    }
  }

  .call-order{
    width: calc(100% + 1.5625em);
    margin-right: -1.5625em;
    padding-left: 7em;
    &__info{
      top: -2.625em;
      gap: 1.5em;
      width: 16.625em;
    }
    &__title{
      font-size: 1.875em;
    }
    &__desc{
      max-width: 12.5em;
    }
    &__img{
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .loyalty-main{
    margin-top: 2.75em;
    padding-top: 2.5em;
    padding-bottom: 17.875em;
    &:before{
      width: 18.25em;
      height: 18.25em;
      left: -4.25em;
      bottom: -4.25em;
      background-color: $yellow;
    }
    .h2,
    &__title{
      font-size: 1.875em;
      line-height: 123%;
    }
    &__desc{
      margin-top: .8125em;
    }
    .partner-location__img{
      right: unset;
      width: 15.8125em;
      left: -.1875em;
      bottom: 1.5em;
    }
    &__coins{
      top: unset;
      bottom: 3.25em;
      width: 6.375em;
      right: -1.0625em;
    }
  }

  .loyalty-advantages{
    .swiper{
      &-slide{
        &-next{
          transform: translateX(-2.25em);
          .advantage-card2{
            background-color: $yellow;
            color: $black;
            .advantage-card2__text{
              color: inherit;
            }
            &:before{
              opacity: 0;
            }
          }
        }
      }
      &-pagination{
        display: flex;
        margin-top: 2.25em;
        position: static;
        &-bullet-active{
          background: #494949;
        }
      }
    }
  }

  .loyalty-advantages2{
    grid-template-columns: 100%;
    &__col{
      order: 2;
    }
  }
}
